body {
    background-color: #0C0934;
    background-position: center;
    background-repeat: no-repeat;
    margin-bottom: 60px;
}
html {
    position: relative;
    min-height: 100%;
}
.footer {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 60px;
    background-color: rgba(0, 0, 0, 0.5);
    color: rgb(255,255,255);
    text-align: center;
    padding-right: 30px;
    font-family: 'VT323', monospace;
    font-size: 30pt;
}
.footer > a:hover {
    text-decoration: overline;
}
